import React from 'react';
import {graphql, Link} from 'gatsby';
import _ from 'lodash';
import urljoin from 'url-join';
import {DiscussionEmbed} from 'disqus-react';
import Layout from '../components/layout';
import SEO from '../components/seo';
import PostDetails from '../components/post-details/post-details';
import {FacebookShareButton, PinterestShareButton, RedditShareButton, TwitterShareButton,} from 'react-share';
import {IoLogoFacebook, IoLogoPinterest, IoLogoReddit, IoLogoTwitter,} from 'react-icons/io';
import {
    BlogPostComment,
    BlogPostDetailsWrapper,
    BlogPostFooter,
    BlogPostFooterEngage,
    PostCollection,
    PostShare,
    PostTags,
} from './templates.style';
import {collectionKnowledge} from '../../content/collection/knowledge';
import RelatedPosts from "../components/related-posts/related-posts";

const BlogPostTemplate = (props: any) => {
    const post = props.data.markdownRemark;

    const edgeFromTags = props.data.byTag.edges;
    const edgeFromCollection = props.data.byCollection.edges;

    const title = post.frontmatter.title;
    const slug = post.fields.slug;
    const siteUrl = props.data.site.siteMetadata.siteUrl;
    const shareUrl = urljoin(siteUrl, slug);

    const disqusConfig = {
        shortname: process.env.GATSBY_DISQUS_NAME,
        config: {identifier: slug, title},
    };
    return (
        <Layout>
            <SEO
                title={post.frontmatter.title}
                description={post.frontmatter.description || post.excerpt}
            />
            <BlogPostDetailsWrapper>
                <PostDetails
                    title={post.frontmatter.title}
                    date={post.frontmatter.date}
                    preview={
                        post.frontmatter.featuredImg.childImageSharp.fluid
                    }
                    description={post.html}
                    imagePosition="left"
                />

                <BlogPostFooter
                    className={post.featuredImg ? '' : 'center'}
                >
                    {post.frontmatter.collection && (
                        <PostCollection className="post_collection">
                            {
                                <Link key={post.frontmatter.collection}
                                      to={`/collection/${post.frontmatter.collection}/`}>
                                    {`Continue exploring ${collectionKnowledge[post.frontmatter.collection].title}`}
                                </Link>
                            }
                        </PostCollection>
                    )}

                    <BlogPostFooterEngage>
                        {post.frontmatter.tags == null ? null : (
                            <PostTags className="post_tags">
                                {post.frontmatter.tags.map((tag: string, index: number) => (
                                    <Link key={index} to={`/tags/${_.kebabCase(tag)}/`}>
                                        {`#${tag}`}
                                    </Link>
                                ))}
                            </PostTags>
                        )}
                        <PostShare>
                            <span>Share This:</span>
                            <FacebookShareButton url={shareUrl} quote={post.excerpt}>
                                <IoLogoFacebook/>
                            </FacebookShareButton>
                            <TwitterShareButton url={shareUrl} title={title}>
                                <IoLogoTwitter/>
                            </TwitterShareButton>
                            <PinterestShareButton
                                url={shareUrl}
                                media={post.featuredImg}
                            >
                                <IoLogoPinterest/>
                            </PinterestShareButton>
                            <RedditShareButton
                                url={shareUrl}
                                title={`${post.frontmatter.title}`}
                            >
                                <IoLogoReddit/>
                            </RedditShareButton>
                        </PostShare>
                    </BlogPostFooterEngage>
                </BlogPostFooter>
                <BlogPostComment
                    className={post.featuredImg ? '' : 'center'}
                >
                    <DiscussionEmbed {...disqusConfig} />
                </BlogPostComment>
            </BlogPostDetailsWrapper>
            <RelatedPosts fromTags={edgeFromTags} fromCollection={edgeFromCollection}/>
        </Layout>
    );
};

export default BlogPostTemplate;

export const indexPostFragment = graphql`
    fragment IndexPostFragment on MarkdownRemark {
        frontmatter {
            title
            tags
            collection
            featuredImg {
                publicURL
                childImageSharp {
                    fluid(maxWidth: 480, maxHeight: 285, quality: 90) {
                        ...GatsbyImageSharpFluid_withWebp
                    }
                }
            }
        }
    }
`

export const pageQuery = graphql`
    query BlogPostBySlug($slug: String!, $tag: [String!], $collection: String) {
        site {
            siteMetadata {
                siteUrl
            }
        }
        markdownRemark(fields: { slug: { eq: $slug } }) {
            id
            excerpt(pruneLength: 160)
            html
            fields {
                slug
            }
            frontmatter {
                title
                date(formatString: "DD MMM, YYYY")
                description
                collection
                tags
                featuredImg {
                    publicURL
                    childImageSharp {
                        fluid(maxWidth: 1170, quality: 90) {
                            ...GatsbyImageSharpFluid_withWebp_tracedSVG
                        }
                    }
                }
            }
        }
        byTag: allMarkdownRemark(
            limit: 6
            sort: { fields: [frontmatter___date], order: DESC }
            filter: {
                frontmatter: { tags: { in: $tag } }
                fields: { slug: { ne: $slug } }
            }
        ) {
            edges {
                node {
                    fields {
                        slug
                    }
                    ...IndexPostFragment
                }
            }
        }
        byCollection: allMarkdownRemark(
            limit: 3
            sort: { fields: [frontmatter___date], order: DESC }
            filter: {
                frontmatter: { collection: { eq: $collection, ne: null }  }
                fields: { slug: { ne: $slug } }
            }
        ) {
            edges {
                node {
                    fields {
                        slug
                    }
                    ...IndexPostFragment
                }
            }
        }
    }
`;
