import * as React from 'react';
import {uniqBy} from "lodash";
import {RelatedPostItem, RelatedPostItems, RelatedPostTitle, RelatedPostWrapper} from "../../templates/templates.style";
import PostCard from "../post-card/post-card";

interface RelatedPostsProps {
    fromTags: any;
    fromCollection?: any;
}

const RelatedPosts: React.FunctionComponent<RelatedPostsProps> = (
    {
        fromTags,
        fromCollection
    }
    ) => {
        const relatedPosts = uniqBy(
            [...fromCollection, ...fromTags],
            obj => obj.node.fields.slug
        ).slice(0, 3);

        return (
            <>
                {
                    relatedPosts.length !== 0 &&
                    <RelatedPostWrapper>
                        <RelatedPostTitle>Related Posts</RelatedPostTitle>
                        <RelatedPostItems>
                            {relatedPosts.map(({node}: any) => (
                                <RelatedPostItem key={node.fields.slug}>
                                    <PostCard
                                        title={node.frontmatter.title || node.fields.slug}
                                        url={node.fields.slug}
                                        image={
                                            node.frontmatter.featuredImg.childImageSharp.fluid
                                        }
                                        tags={node.frontmatter.tags}
                                    />
                                </RelatedPostItem>
                            ))}
                        </RelatedPostItems>
                    </RelatedPostWrapper>
                }
            </>
        );
    }
;

export default RelatedPosts;
